import React, { useEffect, useState } from 'react'
import Heading from '../../components/Heading/Heading'
import Layout from '../../components/Layout/Layout'
// import PokemonCard from '../../components/PokemonCard'
import {IPokemons} from './../../types/pokemons'
import usePokemons from '../../hooks/useData'



const Pokedex: React.FC = () => {
  const {  data,
    isLoading,
    isError} = usePokemons<IPokemons>()
 

  if (isLoading) return <div>Loading...</div>

  if (isError) return <div>Something went wrong!</div>

  // let reg = /as+d/

  return (
    <Layout>
      <Heading tag="h3">{data && data.total} <b>Pokemons</b> for you to choose tour favorite</Heading>
        {data?.pokemons.map((item: any) => ( <div key={item.name_clean}>{item.name_clean}</div>))}
    </Layout>
  )
}

export default Pokedex